import React from 'react';
import Chair from './Chair';

const Legend = ({ price }) => {
    return (
        <div className="buying-scheme__legend">
            <div className="col">
                <div className="buying-scheme__legend-price"><Chair type="standart" />  (<span className="buying-scheme__legend-value">{price?.standart}</span>)</div>
                <div className="buying-scheme__legend-price"><Chair type="vip" />  VIP (<span className="buying-scheme__legend-value">{price?.vip}</span>)</div>
            </div>
            <div className="col">
                <div className="buying-scheme__legend-price"><Chair type="taken" /> </div>
                <div className="buying-scheme__legend-price"><Chair type="selected" /> </div>
            </div>
        </div>
    );
};

export default Legend;
