export default function SubmitButtons({ handleCancel, handleSave }) {

    return (
        <fieldset className="conf-step__buttons text-center">
            <button className="conf-step__button conf-step__button-regular" onClick={handleCancel}></button>
            <input type="submit" value="" className="conf-step__button conf-step__button-accent" onClick={handleSave} />
        </fieldset>
    )
}